.block-reveal {
  --t: calc(var(--td) + var(--d));

  color:transparent;
  padding: 4px;

  position: relative;
  overflow: hidden;

  animation: revealBlock 0s var(--t) forwards;
}

.block-reveal::after {
  content: '';
  width: 0%;
  height: 100%;
  padding-bottom: 4px;
  position: absolute;
  top: 0;
  left: 0;
  /* background: var(--bc); */
  background-image: linear-gradient( 99.9deg, rgba(27,24,31,1) 21.2%, rgb(35, 16, 76) 84.8% );
  
  animation: revealingIn var(--td) var(--d) forwards, revealingOut var(--td) var(--t) forwards;
}


/* animations */
@keyframes revealBlock {
  100% {
    color:#fff;
  }
}

@keyframes revealingIn {

  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes revealingOut {

  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
  }

}

