.page {
  height: calc(100vh - 100px);
  margin: auto;
  width: 100%;
  background: url(./gradient-abstract-5k-zm.jpg) no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: row;
}

.pppagr h1 {
  margin: 0;
  font-size: 100px;
  /* margin-right: auto;
  padding-left: 80px; */
  /* color: antiquewhite; */
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background: -webkit-linear-gradient(#f9f9de, #291f65);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.pppagr h3{
  margin: 0;
  font-size: 24px;
  /* margin-right: auto;
  padding-left: 80px; */
  /* color: antiquewhite; */
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background: -webkit-linear-gradient(#f9f9de, #291f65);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.lab{
  height: 180px;
  width: 180px;
}
.suit{
  height: 100%;
}
@media (max-width: 768px) {
  /* Styles for tablet view */
  .page {
    height: calc(100vh - 100px);
  }

  .lab {
    height: 100px;
    width: 100px;
  }

  .suit {
    height: 60%;
  }
}

@media (max-width: 480px) {
  /* Styles for mobile view */
  .page {
    height: calc(100vh - 60px);
    flex-direction: column;
  }

  .lab {
    height: 80px;
    width: 80px;
  }

  .suit {
    height: 50%;
    overflow: hidden;
  }

  .pppagr h1 {
    font-size: 29px;
  }

  .pppagr h3 {
    font-size: 12px;
  }
}
