/* Base styles for all screen sizes */
.each {
  height: 100%;
  width: 390px;
  padding: 10px;
  border-radius: 10%;
}

.first {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  background: url(./gradient-abstract-5k-zm.jpg);
}

/* Styles for mobile screens (up to 768px) */
@media screen and (max-width: 768px) {
  .each {
    width: 350px; /* Adjust width to full width on smaller screens */
    border-radius: 10%; /* Remove border-radius for a different look */
    height:100%;
    
  }

  .first {
    padding: 10px; /* Add padding to the container for better spacing */
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    text-align: center;
  }
}

/* Styles for tablet screens (768px to 1024px) */
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .each {
    width: 50%; /* Make each item take 50% of the container's width */
  }

  .first {
    gap: 20px; /* Reduce the gap between items for better spacing */
  }
}
.swipe{
     color: white;
     font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}